#!/usr/bin/env node
/**
 * /zest:enable command handler
 *
 * Enables remote persistence (syncing to Zest)
 */

import { loadSettings, saveSettings } from "../config/settings.js";
import { logger } from "../utils/logger.js";

async function main() {
  try {
    const settings = await loadSettings();

    if (settings.enableRemotePersistence) {
      console.log("âœ“ Remote persistence is already enabled");
      return;
    }

    // Enable remote persistence
    settings.enableRemotePersistence = true;
    await saveSettings(settings);

    console.log("âœ… Remote persistence enabled");
    console.log("ðŸ“¤ Data will sync to Zest every 60 seconds");
  } catch (error) {
    logger.error("Failed to enable remote persistence", error);
    console.error(
      "âŒ Failed to enable: " + (error instanceof Error ? error.message : "Unknown error"),
    );
    process.exit(1);
  }
}

main();
